import Map from 'ol/Map.js';
import View from 'ol/View.js';
import { OSM } from 'ol/source.js';
import OlTileLayer from 'ol/layer/Tile.js';

import Vector  from 'ol/source/Vector.js';
import VectorLayer  from 'ol/layer/Vector.js';

import point from 'ol/geom/Point';


import GeoJSON from 'ol/format/GeoJSON.js'; //GeoJson 
import { Style,Stroke,Fill, Text } from 'ol/style.js';  //  

var pnt = new point([126,37]).transform('EPSG:4326', 'EPSG:3857');
var korea = pnt.getCoordinates();

var layer = new OlTileLayer({ 
  source: new OSM()  
});

var myView = new View({
  center: korea,
  zoom: 8
}); //   .

$.ajax({
	url: 'data/ALL_SIGUNGU.json',
	type: 'get',
	contentType: 'application/x-www-form-urlencoded;charset=utf8',
  success: function (data) {
    console.log(data);    
    var format = new GeoJSON({   // GeoJSON  
      featureProjection:'EPSG:3857'
    });
    var parsing = format.readFeatures(data);  //  
    var source = new Vector({  //    
      features : parsing
    });
    var geoVector = new VectorLayer({ //   
      source : source,
      style: (feature, resolution) => {
        let name = feature.values_.adm_nm;
        return new Style({
          stroke: new Stroke({
            color: '#5c68ff',
            width: 1
          }), fill: new Fill({  //
            color: 'rgba( 255, 133, 133 ,0.15)'
          }),
          text: new Text({  //
            text: name,
            textAlign: 'center',
            font: '15px roboto,sans-serif'
          })
        })
      }
    });

    var map = new Map({
      layers: [layer, geoVector],
      target: 'map',
      view: myView
    });
  }
});




